.devDescriptionSeparater{
    border-top: 5px solid rgb(51,51,51)
}
.devEmail{
    cursor: pointer;
}
.blogDevImage{
    margin-right: 10px;
}
.devImage{
    float: left;
}
.blogView{
    overflow-y: scroll;
    overflow-x: hidden;
    height: 100%;

    
    background-color: rgb(51,51,51) !important;
    /* scrollbar-color: rgb(51,51,51) rgb(37,37,37); */
}
.descriptionBlogPost{
    margin-top: 10px;
    font-size: 1.25em;
}

.wrapper {
    max-width: 1050px;
    margin-left:auto;
    margin-right:auto;
    display: grid;
    padding-bottom: 50px;
    background-color: rgb(37,37,37);
    color: white;
    padding: 10px 30px;
}
.container, .container-lg, .container-md, .container-sm {
    max-width: 960px;
}

.img-thumbnail{
    background-color: rgb(59,76,98);
    border: 1px solid rgb(59,76,98);
    max-width: initial;
  }
.blogDeveloperDiv{
    margin-bottom: 10px;
}
.blogTitle{
    text-align: center;
    padding-bottom: 50px;
}
.blogCommentText{
    /* padding: 5px 40px;
    margin-left: 40px; */
    margin-bottom: 10px;
}
.blogCommentText span{
    color: lightgray !important;
    font-size: 11pt !important;
}
.blogStyle{
    margin-bottom: 40px;
    /* background-color: lightgrey; */
    border-radius: 40px;
}

* {
    scrollbar-width: thin;
    /* scrollbar-color: blue orange; */
  } 

  /* chrome coloring for the scrollbars. affects all scroll bars unlike the one for firefox */
  *::-webkit-scrollbar {
    color: rgb(37,37,37);
  }
  *::-webkit-scrollbar-track {
    background: rgb(37,37,37);
  }
  
  *::-webkit-scrollbar-thumb {
    background-color: rgb(51,51,51);
    border: 3px solid rgb(37,37,37);
  }