.noUi-target {
  background: rgb(59,76,98);
  box-shadow: none;
  border-radius: unset;
}

.noUi-handle {
  background: rgb(0, 0, 0);
  box-shadow: none;
  border-radius: unset;
}

.noUi-marker-horizontal.noUi-marker-large {
  height: 25px;
  width: 2.5px;
  background-color: lightgray;
}

.noUi-value.noUi-value-horizontal.noUi-value-large {
  color: lightgray;
  margin-top: -10px;
}

.playbackScrollBar {
  margin: 0px 18px;
}