.editorView {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.editor {
  flex: 1;
}

.playbackControls {
  flex: 0;
}

.aceEditor {
  height: 100%;
  width: 100%;
  padding-right: 10px;
}

.fileTab {
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
  border: 1px gray solid;
  padding: 3px 10px;
}

.fileTab.activeFile {
  border: 1px lightgray solid;
  border-bottom: none;
  background-color: black;
}

.selectedCodeHighlight {
  background-color: rgb(111, 177, 119) /*#C7E0F1*/;
  opacity: 27%;
  position: absolute;
}

.surroundingCodeHighlight {
  background-color: rgb(111, 177, 119) /*#9eacb6*/;
  opacity: 3%;
  position: absolute;
}

.newCodeHighlight {
  position:absolute;
  z-index:10; 
  opacity: 60%;
  border-bottom-style: dotted;
  border-bottom-width: 3px;
  border-bottom-color: rgb(33, 130, 36);
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}

.insertOnLine {
  color: rgb(161, 179, 167);
  font-weight: bold;
}

.deleteOnLine {
  text-decoration-line: underline;
  text-decoration-color: red;
  text-decoration-style: solid;
}

.fileUpdated::before {
  content: "*";
}