.commentView {
  border: 1px gray solid;
  /* margin: 5px 3px; */
  padding: 5px 10px;
}

.activeComment {
  background-color: rgb(59,76,98);
}

.commentTopBar {
  border-bottom: rgb(83, 84, 86) 1px solid;
  margin-bottom: 8px;
  overflow-y: auto;
}

.commentCount {
  font-size: .95em;
  display: inline;
}

.commentText {
  padding: 2px 5px;
}

.commentAvatar {
  display: inline;
}

.titleBar {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.15em;
  padding: 8px 0px;
}

.btn-speed{
  background-color: rgb(59,76,98);
  color: rgb(223, 242, 244);
  margin: 1px;
  font-size: .8em;
}